import { Directive, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import {
  AppNameService, EventbusService,
  MainBarTitleService, scrollToTop, WindowService
} from '@front-shared-lib';
import {
  DeliveryManager, navByBus
} from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { ListComposeAdminPanel } from '../../defines/composition/list';

@Directive()
export class DeliveryManagerListBase
implements OnInit, OnDestroy {
  listCompose = new ListComposeAdminPanel<DeliveryManager>(
    DeliveryManager,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;
  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (deliveryManager: DeliveryManager) => this.viewDeliveryManager(deliveryManager)
    },
    {
      label: 'Update',
      icon: 'edit',
      event: (deliveryManager: DeliveryManager) => this.updateDeliveryManager(deliveryManager)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (deliveryManager: DeliveryManager) => this.listCompose.openDeleteOneModal(
        deliveryManager._id,
        'Delete Delivery Manager',
        'Are You Sure You Want To Delete This Delivery Manager?'
      )
    }
  ];

  constructor(
    protected router: Router,
    protected eventbus: EventbusService,
    protected windowService: WindowService,
    protected toastr: ToastrService,
    protected apNme: AppNameService,
    protected mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Delivery Managers - List of Delivery Managers';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `This page provides an overview of delivery managers,
      allowing users to manage and view delivery manager details effectively.`
    });

    this.listCompose.getDataArr();
    scrollToTop(this.windowService);
  }

  updateDeliveryManager(deliveryManager: DeliveryManager) {
    this.navigateByBus(['/delivery-manager/update'], 'updateDeliveryManager', deliveryManager);
  }

  async deleteOne(deliveryManager: DeliveryManager) {
    await deliveryManager.remove();
  }

  outEvent(ev) {
    switch (ev.type) {
      case 'update':
        this.updateDeliveryManager(ev.val);
        break;
    }
  }

  viewDeliveryManager(deliveryManager: DeliveryManager) {
    this.navigateByBus(['/delivery-manager/view/' + deliveryManager.urId], 'oneItem', deliveryManager);
  }

  subscribeToPkg(id: string) {
    this.router.navigate(['subscribe/' + id]);
  }

  addDeliveryManager() {
    this.router.navigate(['/delivery-manager/add']);
  }

  getUser(dm: DeliveryManager) {
    return dm.manager;
  }

  viewActivity(parent: string, header: string) {
    this.navigateByBus(['/activity/'], 'viewActivity', {
      parent,
      header
    });
  }

  ngOnDestroy() {
    this.listCompose.cleanup?.();
  }
}

